@import url(https://fonts.googleapis.com/css2?family=Azeret+Mono:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    font-family: 'Azeret Mono', monospace;
}

.App{
    background-color: #d9ffd8;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.drum-setup{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #222222;
    padding: 30px;
    border-radius: 5px;
    border: 2px white solid;
    box-shadow: 0px 6px 6px 2px rgba(0,0,0,0.5);
}

#display{
    height: 20px;
    overflow: hidden;
    width: 100px;
    padding:10px;
    border-radius: 5px;
    background-color: #2b90f5;
    border: 2px white solid;
    color: white;
    
    display: flex;
    justify-content: center;
    align-self: center;
}


#right-side{
    display: flex;
    flex-direction: column;
    grid-gap:5vh;
    gap:5vh;
}

#turn-on-off{
    display: flex;
    justify-content: center;
    align-self: center;
}

#turn-on-off button{
    border:none;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    box-shadow: 0px 2px 2px 2px black;
    border: 2px white solid;
    color:white;
}

#turn-on-off button:hover{
    background-color: white;
    color: grey;
}

.red{
    background-color: tomato;
}

.green{
    background-color: turquoise;
}


.grid{
    padding: 20px;
    display:grid;
    grid-gap:10px;
    gap:10px;
    grid-template-columns: repeat(3,50px);
}
.drum-pad{
    padding: 20px;
    border: 1px black solid;
    background-color: #fcc8ff;
    border-radius: 2px;
    box-shadow: 0px 2px 2px 2px black;
    border: 2px white solid;
    display: flex;
    justify-content: center;
    align-items: center;
}

.drum-pad:hover{
    background-color: #c9aeff;
}
